import React, { useState, useEffect } from "react";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Dialog from "@mui/material/Dialog";
import IconButton from "@mui/material/IconButton";
import { Close as CloseIcon } from "@mui/icons-material";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import useFetchTrailer from './useFetchTrailer';
import ReactPlayer from 'react-player';

const ModalContent = ({ item }) => {
  const [imageLoaded, setImageLoaded] = useState(false);
  const { trailer, loading } = useFetchTrailer(item.id);

  useEffect(() => {
    const preloadImage = new Image();
    preloadImage.src = `https://image.tmdb.org/t/p/w500/${item.backdrop_path}`;
    preloadImage.onload = () => {
      setImageLoaded(true);
    };
  }, [item.backdrop_path]);

  return (
    <div>
      <div
        style={{
          position: "relative",
          width: "100%",
          paddingTop: "56.25%", // 16:9 aspect ratio
          overflow: "hidden",
          boxShadow: "0 0 0 1px #dadce0",
          display: imageLoaded ? "block" : "none",
        }}
      >
        {!loading && trailer && trailer.results.length > 0 ? (
          <ReactPlayer
            url={`https://www.youtube.com/watch?v=${trailer.results[0].key}`}
            controls={true}
            width="100%"
            height="100%"
            style={{
              position: "absolute",
              top: "0",
              left: "0",
            }}
          />
        ) : (
          <img
            src={`https://image.tmdb.org/t/p/w500/${item.backdrop_path}`}
            alt="Backdrop Image"
            style={{
              position: "absolute",
              top: "0",
              left: "0",
              width: "100%",
              height: "100%",
              objectFit: "cover",
            }}
          />
        )}
      </div>
      <h2
        style={{
          margin: "1rem",
          overflow: "hidden",
          textOverflow: "ellipsis",
          display: "-webkit-box",
          WebkitLineClamp: "2",
          WebkitBoxOrient: "vertical",
          color: "#70757a",
          textAlign: "left",
        }}
      >
        {item.title}
      </h2>
      <p
        style={{
          margin: "1rem",
          textAlign: "left",
          color: "#70757a",
          display: "inline-block",
        }}
      >
        {item.overview.length > 100
          ? item.overview.slice(0, 100) + "... "
          : item.overview}
        {item.overview.length > 100 && (
          <a
            style={{
              color: "#007BFF",
              display: "inline-flex",
              alignItems: "center",
            }}
            onClick={() => {
              // Handle read more functionality
            }}
          >
            more <ExpandMoreIcon />
          </a>
        )}
      </p>
    </div>
  );
};

const ModalComponent = ({
  selectedItemIndex,
  handleModalClose,
  handleModalNavigation,
  likedItems,
  drawerWidth,
}) => {
  const settings = {
    initialSlide: selectedItemIndex,
    afterChange: handleModalNavigation,
    dots: false,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    swipeToSlide: true,
  };

  return (
    <Dialog
      open={selectedItemIndex !== null}
      onClose={handleModalClose}
      classes={{ paper: "custom-modal-paper" }}
      PaperProps={{
        style: {
          margin: "0",
          height: "100vh",
          position: "fixed",
          right: "0",
          top: "0",
          maxHeight: "unset",
          maxWidth: "unset",
        },
      }}
    >
      <div style={{ width: drawerWidth, position: "relative" }}>
        {selectedItemIndex !== null && (
          <>
            <IconButton
              style={{
                position: "absolute",
                top: "16px",
                right: "16px",
                color: "#fff",
                zIndex: "1",
                backgroundColor: "rgb(255, 255, 255, 0.1)",
              }}
              onClick={handleModalClose}
            >
              <CloseIcon />
            </IconButton>
            <Slider {...settings}>
              {likedItems.map((item, index) => (
                <div key={index}>
                  <ModalContent item={item} />
                </div>
              ))}
            </Slider>
          </>
        )}
      </div>
    </Dialog>
  );
};

export default ModalComponent;
